/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>The other day I referenced <a href=\"http://janchipchase.com\">Jan Chipchase's blog</a> as \"almost certainly my favorite.\" This is patently false. I am a big fat liar when I get excited. (Chipchase is probably tied for second with a few other exceptionally cool writers.)</p>\n<p>The blogger at the top of my feedreader is, really this time, absolutely, <a href=\"http://www.ethanzuckerman.com/blog/\">Ethan Zuckerman</a>. I got into his blog when I was getting my journalism degree and I stumbled across \"<a href=\"http://h2odev.law.harvard.edu/ezuckerman/\">Global Attention Profiles</a>,\" basically maps of media blindspots. He was doing all of this research that combined media analysis with mapping with data scraping. Very cool.</p>\n<p>When I was reading about that project in 2003 or 2004 I was totally turned on by the idea of writing scripts to create DIY datasets; I had no idea how to program at the time, but <strike> the stuff I have done with perl scrapers </strike> my whole new career in programming is directly attributable to Ethan's inspiration 2 years prior.</p>\n<p>So I kept reading his blog, and it got me into all freaking KINDS of stuff, among them (<strong>this is just the stuff that I directly attributed to him, by name, and took the time to write a blog post about</strong>):</p>\n<ul>\n<li><a href=\"http://www.unthinkingly.com/2005/09/14/geeks-responding-to-katrina-relief-20/\">The Katrina Peoplefinder Project</a> Absolutely. Incredible. Project.</li>\n<li><a href=\"http://www.unthinkingly.com/2005/07/14/technology-for-the-poor-profit-for-the-rich/\">Cell Phones in the Developing World</a></li>\n<li><a href=\"http://www.unthinkingly.com/2005/07/21/exporting-technology-exporting-ideas/\">Geekcorps</a> (Zuckerman was a founder)</li>\n<li><a href=\"http://www.unthinkingly.com/2006/12/11/geekcorps-writeup-on-newsforge/\">Hack Your on \"Cantenna\" for Bandwidth in Mali</a></li>\n<li><a href=\"http://www.unthinkingly.com/2006/06/29/the-reality-of-the-open-source-desktop-in-developing-world/\">Open Source in the Developing World</a></li>\n<li><a href=\"http://www.unthinkingly.com/2007/01/31/the-last-mile-wikipedia-launches/\">The Last Mile Wikipedia</a></li>\n<li><a href=\"http://www.unthinkingly.com/2005/12/28/sino-blogosphere-is-on-the-map/\">The Chinese Blogosphere</a></li>\n<li><a href=\"http://www.unthinkingly.com/2005/11/16/fear-and-loathing-in-tunis/\">The World Summit on the Information Society</a> (This guy has incredible liveblogging prowess)</li>\n<li><a href=\"http://www.unthinkingly.com/2005/11/11/hooray-for-progressive-podcasting/\">Progressive Podcasting</a></li>\n<li><a href=\"http://www.unthinkingly.com/2005/11/11/more-accounts-of-the-1-laptop-per-child-laptop-at-mit/\">The One Laptop Per Child Initiative</a></li>\n<li><a href=\"http://www.unthinkingly.com/2005/09/22/the-dissident-blogger-handbook/\">The \"Dissident\" Blogger Handbook</a> (Or how to blog *really* anonymously)</li>\n<li><a href=\"http://www.unthinkingly.com/index.php?tag=zuckerman\">Conflict Diamonds</a></li>\n</ul>\n<p>Ethan writes (at length, with incredible detail, humor and rich references) about the developing world at <a href=\"http://www.ethanzuckerman.com/blog/\">http://www.ethanzuckerman.com/blog/</a>. As evidenced by the number of links above, <strong>I AM JUST A LAME HACK RIPPING THIS GUY OFF AS BEST I CAN.</strong> And I'm ok with that. He's just that cool.</p>","frontmatter":{"created_at":"February 04, 2008","path":"/2008/02/04/ode-to-ethan","title":"Ode To Ethan","tags":["ethan"]}}},"pathContext":{}}

/***/ })

